/* SMU Brand Font Classes */
.smu-sans-serif {
  font-family: Arial, sans-serif;
}

.smu-serif {
  font-family: Georgia, serif;
}

body {
  margin: 0;
  font-family: Arial, sans-serif; /* SMU-approved fallback */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Georgia (serif) for odd-numbered headings */
h1, h3, h5 {
  font-family: Georgia, serif;
}

/* Arial (sans-serif) for even-numbered headings */
h2, h4, h6 {
  font-family: Arial, sans-serif;
}
